@import '../../styles/_colors';
@import '../../styles/_fonts';



.TestimonialsBackground { 
    padding: 1rem;
    
}

.TestimonialsContainer {
    
    background: $lila_suave;
    border-radius: 1rem;
    margin: 0 auto;
    margin-top: 30px;
    margin-bottom: 60px;
    max-width: 1140px;
    min-height: 20rem;
    height: auto;

    display:flex;
    flex-direction: column;
    align-items: center;
    .quotes{
        display: block;
        padding: 1rem;
        img{
            height: 2rem;
            opacity: 0.1;
        }   
    }
    .selectedName{
        color: rgb(69, 2, 95);
        font-size: 2rem;
        
    }
    .selectedTestimonial{
        color: $lila;
        font-size: 1rem;
        padding: 2rem 15rem;
        text-align: center;
    }
    .button{
        margin: 3rem 0;
        cursor: pointer;
        padding: 0.5rem 2rem;
        border: 1px solid rgb(69, 2, 95);
        border-radius: 1rem;
        background: transparent;
        color: $lila;
        font-weight: 600;

        transition: all 300ms ease-in-out;
    }
    .button:hover{
        background: $lila;
        color: white;
    }
    .doctoralia{
        color: $lila;
        font-weight: 400;
        padding: 1rem;
        text-align: center;
        
    }
    .doctoralia a{
        text-decoration: none;
        font-weight: 600;
    }

    .doctoralia a:hover{
        color: rgb(69, 2, 95);
        font-weight: 700;
    }
}

@media (max-width:875px) {
    .TestimonialsContainer{

        .selectedTestimonial{
            padding: 2rem 10rem;
        }
    }
    
}

@media (max-width:675px) {
    .TestimonialsContainer{

        .selectedTestimonial{
            padding: 2rem 5rem;
        }
        .button{
            margin: 2rem 0;
        }
    }
    
}

@media (max-width:375px) {
    .TestimonialsContainer{

        .selectedTestimonial{
            padding: 1rem 1.5rem;
        }
        .button{
            margin: 1rem 0;
            background: $lila;
            color: white;
        }
        .doctoralia{
            font-size: 0.8rem;
        }
    }
    
}
