@import '../../styles/_colors';
@import '../../styles/_fonts';

.Bio{

    display: grid;
    grid-template-areas:
     "foto title"
     "foto text"
     "foto button"
     "logos logos"
    ;
    grid-template-columns: 20% 80%;
    margin: 0 auto;
    margin-top: 65px;
    margin-bottom: 40px;
    max-width: 1140px;
    height: auto;

    &__image{
        margin: 0 auto;
        grid-area: foto;
    }

    &__title{
        display: flex;
        align-items: flex-end;
        justify-content: center;
        grid-area: title;

        color: $verde_oscuro;
        padding-bottom: 3rem;
    }

    &__text{
        grid-area: text;
        text-align: center;
        padding: 0 13rem;
        font-size: 1.2rem;
        text-justify: distribute;
        padding-bottom: 2rem;
    }    
    &__logos{
        margin-top: 3rem;
        display: flex;
        justify-content: center;
        grid-area: logos;
        img{
            height: 2.2rem;
            padding: 2rem 1rem;
        }
    }

}

@media (max-width: 768px){
    .Bio{
        display: grid;
        grid-template-areas:
        "foto"
        "title"
        "text"
        "button"
        "logos";
        grid-template-columns: 100%;
        &__image{
            max-width: 70%;
        }
        &__title{
            margin-top: 2rem;
            padding: 1rem;
            font-size: 2rem;
        }
        &__text{
            padding: 1rem;
            font-size: 1rem;
        }
        &__button{
            margin-top: 1rem;
        }
        &__logos{
            display: none;
            grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));

            margin-top: 2rem;
            img{
                padding: 0.9rem;
                margin: 0 auto;
            }
        }
    }

}