@import '../../styles/_colors';
@import '../../styles/_fonts';


.CV__container{
    margin-top: 150px;
    padding: 1rem;

    display: grid;
    grid-template-areas: "foto1 void text text"
    "foto1 foto2 text text"
    "empty foto2 text text";
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-auto-rows: 20rem 20rem 10rem;
    gap: 1.5rem;

    

    .CV__foto1{
    
        grid-area: foto1;
        
        img{
            width: 100%;
            height: 30rem;   
            object-fit: cover;
            transition: all 300ms ease-in;
        }
    }
    .CV__foto2{
        
        grid-area: foto2;
        img{
            width: 100%;
            height: 30rem;
            object-fit: cover;
            object-position: 100% 100%;
            transition: all 300ms ease-in;  
        }   
    }

    img:hover {
        transform: scale(1.1); 
    }
    .CV__text{
        grid-area: text;
      

        h1{
            color: $lila;
            font-size: 4rem;
            font-weight: 500;
            padding-left: 2rem;
            padding-bottom: 2rem;
        }
    
        h2{
            color: #843187;
            font-size: 2rem;
            padding: 2rem;
            padding-left: 2rem;
            font-weight: 500;
            background: $lila_suave;
            margin-bottom: 2rem;
        }
        p{
            padding-left: 2rem;
            padding-bottom: 3rem;
            font-size: 1.3rem;
        }
        
    }  
}

@media (max-width: 1470px){
    .CV__container{
    margin-top: 100px;
    grid-template-areas: "foto1 text";
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: auto;
    gap: 2rem;

    padding: 2rem;
    margin-bottom: 1rem;
    

        .CV__foto1{
            img{
                height: 50rem;
            }
        }
        .CV__foto2{
            display: none;
        }
        .CV__text{
            h1{
                font-size: 3rem;
                padding-bottom: 1rem;
            }
        
            h2{
                font-size: 1.2rem;
                padding-left: 2rem;
                padding-bottom: 2rem;
            }
            p{
                font-size: 1.2rem;
                padding-left: 0rem;
            }
            
        } 

    }
}

@media (max-width: 900px){
    .CV__container{
    margin-top: 70px;
    grid-template-areas:
    "foto1"
    "text";
    grid-template-columns: 1fr;
    grid-auto-rows: auto;

        .CV__foto1{
            img{
                height: 20rem;
            }
        }
        .CV__text{
            
            h1{
                font-size: 3rem;
                padding-bottom: 1rem;
            }
        
            h2{
                font-size: 1rem;
                padding-left: 2rem;
                padding-bottom: 2rem;
            }
            p{
                text-align: center;
                font-size: 1rem;
                padding-left: 0rem;
            }
            
        } 

    }
}
