@import '../../styles/_colors';
@import '../../styles/_fonts';

.Contact__form-section {
    display: grid;
    grid-template-areas: "form img";
    grid-template-columns: 1fr 1fr;
    padding: 1rem;

    .Contact__form{
        display: grid;
        grid-area: form;

        h3{
            font-size: 3rem;
            color: $lila;
            font-weight: 500;
            padding-bottom: 2rem;
        }

        input, textarea{
            background-color: $lila_suave;
            display: inline-block;
            border-radius: 1rem;
            padding: 1rem 1rem;
            margin: 1rem 0;
            resize: none;
        }

        input::placeholder, textarea::placeholder {
            color: $lila;
        }

        &-name{
            height: auto;
            border-style: none;
        }
        &-email{
            border-style: none;
        }
        &-msg{
            height: 5rem;
            border-style: none;
        }
        
        &-submit{
            cursor: pointer;
            font-weight: 700;
            color: $lila;
            background: $lila_suave;
            border: 1px solid $lila;

            transition: all 300ms ease-in;
        } 
        &-submit:hover{
            color: white;
            background: $lila;
        }
        &-sended{
            display: block;
            background: rgba(40, 190, 40, 0.589);
            font-weight: 500;
            border-radius: 1rem;
            color: white;
            padding: 1rem;
        }
    }

    .About__img{
        width: 70%;
        margin: 0 auto;
        
    }

}

@media(max-width: 767px){

    .Contact__form-section {
        margin-top: -100px;
        margin-bottom: -150px;
        grid-template-areas: 
        "img"
        "form";
        grid-template-columns: 1fr;
        padding: 1rem;

        .Contact__form{
            margin-top: 20px;
            
            .About__img{
                width: 100%;
                margin: 0;
                
            }
        }
    }
}

