
.HeroArrow{
    position: absolute;
    top: 500px;
    left: 50%;
    
    img{
        opacity: 0.3;
        width: 3rem;
        transition: all 300ms ease-in-out;
    }
    img:hover{
        cursor: pointer;
        opacity: 1;
    }
}

@media (max-width: 768px){
    .HeroArrow{
        display: none;
    }
}