@import '../../styles/_colors';
@import '../../styles/_fonts';

.LinkButton{
    display: flex;
    justify-content:center;
    align-items:center;

    text-decoration: none;
    border-style: none;
    margin: 0 auto;
    grid-area: button;
    font-size: 1rem;
    font-weight: 600;
    width: fit-content;
    height: 3rem;
    padding: 0 2rem;
    color: white;
    background-color: $verde_claro;
    border-radius: 5rem;

    transition: all 300ms ease-in-out;
}

.LinkButton:hover{
    cursor: pointer;
    background: $verde_oscuro;
}