
.logos{
    margin: 1rem 0;
    display: flex;
    justify-content: center;
    grid-area: logos;
    img{
        height: 2.2rem;
        padding: 2rem 1rem;
    }
}

@media (max-width: 768px){ 
    .logos{
        display: none;
        grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));

        margin-top: 2rem;
        img{
            padding: 0.9rem;
            margin: 0 auto;
        }
    }
}
