@import '../../styles/_colors';
@import '../../styles/_fonts';


.Footer{
    background: $lila;
}

.Footer__content{
    display: grid;
    grid-template-areas: 
    "logoR logoTitle contact"
    "menu social contact";
    grid-template-rows: 5rem auto;
    
    width: 1140px;
    padding: 4rem 0;
    margin: 0 auto;


    .Footer__r{
        grid-area: logoR;
        display: flex;
        justify-content: center;
        
        img{
            height: 5rem;
        }
    }
    .Footer__menu{
        grid-area: menu;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        padding: 2rem 0;
        list-style: none;

        .list-item{
            padding: 0.5rem 0;
        }
        a{
            text-decoration: none;
            color: white;
            transition: all 200ms ease-out;
        }
        a:hover{
            color: $verde_logo;
            
        }
    }
    .Footer__logotitle{
        grid-area: logoTitle;
        display: flex;
        justify-content: center;

        img{
            max-height: 5rem;
            
        }
    }
    .Footer__social{
        grid-area: social; 
        display: flex;
        justify-content: center;

        img{
            padding: 2rem;
            height: 2rem;
        }
    }

    .Footer__contactMe{
        grid-area: contact;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        

        h3{
            font-size: 2rem;
            color: white;
            font-weight: 400;
            padding-bottom: 2rem;
        }

       

        &-whatsApp:hover > img{
            filter: grayscale(0%);
        }

        &-email:hover > img{
            filter: grayscale(0%);
        }


        a{
            display: flex;
            align-items: center;
            justify-content: center;


            text-decoration: none;
            color: white;
            transition: all 200ms ease-out;

            p{
                padding: 1rem;
            }
            img{
                height: 2rem;
                filter: grayscale(100%);
                transition: all 200ms ease-out;
            }
        }

        
        
    }   

}

.Footer__createdBy{
    display:flex;
    align-items: center;
    justify-content: center;

    background: $lila_suave;
    color: $lila;
    padding: 1rem;
    p{
        font-weight: 500;
    }
    a{
        text-decoration: none;
        cursor: pointer;
        color: $lila;
        font-weight: 700;
        transition: all 300ms ease-in-out;
    }
    a:hover{
        color: rgb(42, 2, 37);
    }

}  



@media(max-width:1200px){
    
    .Footer__content{
        width: 90%;
        grid-template-areas: 
        "logoTitle"
        "social"
        "contact";
        padding: 4rem 1rem;
        padding-top: 2rem;
        padding-bottom: 4rem;

        .Footer__r, .Footer__menu{
            display: none;
        }
        .Footer__contactMe{
            width: auto;
            h3{
                display: none;
            }
            a{
                img{
                    filter: grayscale(0%);
                }
            }
        }

    }

}